.friendList {
  margin: 50px;
  list-style: none;
  padding: 0;
  display: flex;
  align-items: center;
  flex-direction: column;
}

.item {
  display: flex;
  align-items: center;
  padding: 15px;
  margin: 20px;
  width: 430px;
  /* border: 1px solid #ba3535; */
  border: 1px solid #5016c5;
  border-radius: 5px;
  background-color: #fdfdfd;
  box-shadow: 0 5px 7px -1px rgba(51, 51, 51, 0.23);
}

.avatar {
  height: 80px;
  width: 80px;
  border-radius: 5px;
  margin-right: 10px;
}

.name {
  font-size: 30px;
  font-weight: 600;
}

.status {
  display: inline-block;
  margin-right: 10px;
  margin-left: 10px;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background: green;
}
